@font-face {
  font-family: 'Poppins';
  font-weight: 700;
  font-style: normal;
  src: local('Poppins Bold'), local('Poppins-Bold'),
    url('./fonts/poppins_700.woff2') format('woff2'),
    url('./fonts/poppins_700.woff') format('woff');
}
@font-face {
  font-family: 'Poppins';
  font-weight: 600;
  font-style: normal;
  src: local('Poppins SemiBold'), local('Poppins-SemiBold'),
    url('./fonts/poppins_600.woff2') format('woff2'),
    url('./fonts/poppins_600.woff') format('woff');
}
@font-face {
  font-family: 'Poppins';
  font-weight: 500;
  font-style: normal;
  src: local('Poppins Medium'), local('Poppins-Medium'),
    url('./fonts/poppins_500.woff2') format('woff2'),
    url('./fonts/poppins_500.woff') format('woff');
}
@font-face {
  font-family: 'Poppins';
  font-weight: 400;
  font-style: normal;
  src: local('Poppins Regular'), local('Poppins-Regular'),
    url('./fonts/poppins_400.woff2') format('woff2'),
    url('./fonts/poppins_400.woff') format('woff');
}
body {
  background-image: url('./img/background.png');
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  font-family: 'Poppins', sans-serif;
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  a {
    text-decoration: none;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
  }
  ul {
    padding: 0;
    li {
      list-style: none;
    }
  }
  button {
    border: none;
    outline: none;
    cursor: pointer;
    object-fit: contain;
  }
  img {
    max-width: 84px;
    box-shadow: 0px 4px 24px 0px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
  }
  div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 464px;
    margin: 0 auto;
    padding-top: 25px;
    h1 {
      font-size: 36px;
      color: #ffffff;
      font-weight: 700;
      line-height: 45px;
      padding-bottom: 8px;
      text-transform: uppercase;
    }
    .startBtn {
      margin: auto;
      font-size: 18px;
      color: #1d355d;
      font-weight: 600;
      line-height: 27px;
      padding: 1rem 2rem;
      align-self: center;
      border-radius: 12px;
      background-color: #ffffff;
    }
    .container {
      margin: auto;
      max-width: 400px;
      padding: 30px;
      border-radius: 12px;
      background-color: white;
      position: relative;
      display: flex;
      flex-direction: column;
    }
    .container::before {
      content: '';
      width: 32%;
      height: 24%;
      position: absolute;
      top: -13%;
      left: 68%;
      background-image: url('./img/undraw_adventure.svg');
      background-position: right 100%;
      background-repeat: no-repeat;
      background-size: 100% 114%;
    }
  }
}

